import {
	ILevel, World, Mob,
	Item, EquipableItem, FoodStore,
	Activity, Quest, KillQuest, Progress,
	TravelActivity, LevelStore, Ability, AbilityStore,
	PotionStore,
	LearnActivity
} from '../../world';


export interface IArmor {
	qualityColor?: string;
	slot?: string;
	strength?: number;
	stamina?: number;
	armor?: number;
	spirit?: number;
	intellect?: number;
	wisdom?: number;
}

export class LevelTools {


	static createArmor(icon: string, name: string, stat: IArmor): EquipableItem {
		const item = new EquipableItem(name);
		item.icon = icon;
		item.qualityColor = "green";
		Object.assign(item, stat);
		return item;
	}

	static createWeapon(icon: string, name: string, damage: number
		, damageCooldown: number, stat: IArmor): EquipableItem {
		const item = new EquipableItem(name);
		item.icon = icon;
		item.slot = "two-hand";
		item.qualityColor = "green";
		item.damage = damage;
		item.damageCooldown = damageCooldown;
		Object.assign(item, stat);
		return item;
	}

	static createMob(icon: string, name: string, life: number, dps: number): () => Mob {
		return () => new Mob(icon, name, life, dps);
	}
	static createKillQuest(levelName: number, mob: () => Mob, quantity: number, rewards: Item | Item[],
		questName: string = null): KillQuest {
		const quest = new KillQuest(mob, quantity);
		quest.id = 'level_' + levelName + '_kill_' + quantity + "_" + quest.mobSample.name;
		if (questName) {
			quest.name = questName;
		}
		if (rewards) {
			if (rewards instanceof Array) {
				for (let reward of rewards) {
					quest.rewards.push(reward);
				}
			} else {
				quest.rewards.push(rewards);
			}
		}
		return quest;
	}


	static createFastMob(icon: string, name: string, armor:number, life: number, dps: number): () => Mob {
		return () => {			
			const visibleLife = Math.round(1 + (life / (10 + armor))) * 10;
			const mob = new Mob(icon, name, visibleLife, dps);
			mob.weaponCooldown = 15;
			mob.weaponDamage = Math.round(mob.weaponCooldown * dps / 20);
			mob.characterStat.armor.initialValue = armor;
			mob.addAndUseAbility(AbilityStore.cristalShield);
			mob.refreshStat();
			return mob;
		}
	}

}


