import {
	Activity, ITask, Group, ILevel, TaskProgress,Player
} from '../../world';



export class RecruitActivity extends Activity {		
	constructor(
		public player: Player
	) {		
		super(`Recruit a ${player.profession}`);				
		this.icon = player.icon;
		this.id = 'recruit ' + player.name;
		this.description = `Ask ${player.name} to join your group.`;
	}
	
	public start(group: Group): ITask {
		return new TaskProgress('Recruiting ' + this.player.name, 20);
	}

	onQuestSuccess(group : Group){		
		group.world.players.push(this.player);
		group.includePlayer(this.player);			
	}
}


