import {
	Item, EquipableItem, Food, StackItem,
	Tools, Player, Potion
} from '../../world';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';



export class Bag {
	public stacks: StackItem[] = [];
	$ :BehaviorSubject<Bag> = new BehaviorSubject(this);

	public dispatch(){
		this.$.next(this);
	}

	add(item: Item) {
		for (let stack of this.stacks.filter(s => s.item === item)) {
			const stackPlace = item.stackSize - stack.quantity;
			if (stack.quantity < item.stackSize) {
				stack.quantity++;
				this.dispatch();
				return;
			}
		}
		this.stacks.unshift(new StackItem(item, 1));
		this.dispatch();
	}

	remove(item: Item) {
		for (let stack of this.stacks.filter(s => s.item === item)) {
			if (stack.quantity >= 2) {
				stack.quantity--;
			} else {
				Tools.removeItem(this.stacks, stack);
			}
			this.dispatch();
			return;
		}
	}

	getFoods(): StackItem[] {
		return this.stacks.filter(s => (<Food>s.item).isFood);
	}

	getPotions(): StackItem[] {
		return this.stacks.filter(s => (<Potion>s.item).isPotion);
	}

	getEquipables(): Item[] {
		return this.stacks.filter(s => (<EquipableItem>s.item).isEquipable).map(s => s.item);
	}

	popFood(): Food {
		const food = this.getFoods()[0];
		if (food) {
			this.remove(food.item);
			return <Food>food.item;
		}
		return null;
	}

	popPotion(): Potion {
		const stack = this.getPotions()[0];
		if (stack) {
			this.remove(stack.item);
			return <Potion>stack.item;
		}
		return null;
	}

	equip(player: Player, item: EquipableItem) {
		player.equip(item, this);
	}

	unequip(player: Player, item: EquipableItem) {
		player.unequip(item, this);
	}

	countItems(item: Item): number {
		return this.stacks
			.filter(s => s.item === item)
			.map(s => s.quantity)
			.reduce((a, b) => a + b, 0);
	}

}






