import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Input, Output, EventEmitter } from '@angular/core';
import { Player, Tools } from '../world/';
import { Focus } from '../layout/focus';

@Component({
  selector: 'app-player-icon',
  templateUrl: './player-icon.component.html',
	styleUrls: ['./player-icon.component.css'],
	changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PlayerIconComponent implements OnInit {

	@Input() player: Player;
	dps$;
	currentLife$;
	currentMana$;

  constructor() { }
  ngOnInit() {
		this.dps$ = this.player.$.map(p=>p.getWeaponDps());
		this.currentLife$ = this.player.$.map(p=>Math.floor(p.currentLife));
		this.currentMana$ = this.player.$.map(p=>Math.floor(p.currentMana));
	}
	ngOnChanges() {}

}



