import { Component, OnInit } from '@angular/core';
import { Input, Output } from '@angular/core';
import { Focus } from '../layout/focus';
import {
	ActivityProgress, Player, Mob, Fight,
	EquipableItem
} from '../world/';



@Component({
	selector: 'app-activity',
	templateUrl: './activity.component.html',
	styleUrls: ['./activity.component.css']
})
export class ActivityComponent implements OnInit {

	@Input() focus: Focus;
	@Input() player: Player;
	activity: ActivityProgress;
	mob: Mob;
	


	constructor() { }

	ngOnInit() { }

	ngOnChanges() {
		this.activity = this.focus.activityProgress;		
		console.log("ngChanges");
	}

	getMob() {
		const fight = <Fight>this.activity.thread.allTasks[this.activity.thread.allTasks.length - 1];
		this.mob = fight ? fight.mob : null;
		return this.mob;
	}

	done() {
		this.focus.refresh();
	}

	selectTab(tabName: string) {		
		this.focus.selectTab(tabName);
		this.focus.refresh();
	}

	hasEquipableReward() : boolean{
		return this.activity.group.foundItems.some(item => (<EquipableItem>item).isEquipable);
	}
}



