

import {
	ITask, Group, KillQuest, TaskProgress, Fight, LunchTask
} from '../../world';




export class KillQuestTask implements ITask {
	public name: string;
	public progress = '';
	constructor(
		private group: Group,
		private quest: KillQuest) {
		this.name = quest.name;
		this.progress = `0/${this.quest.quantity}`
	}
	public *start() {
		yield new TaskProgress("Go to location", 12);
		for (let i = 0; i < this.quest.quantity; i++) {
			const lunch = LunchTask.tryGetLunch(this.group);
			if (lunch) {
				yield lunch;
			}

			const success = yield new Fight(this.group, this.quest.mobBuilder());
			if (!success) {
				return false;
			}
			this.progress = `${i+1} / ${this.quest.quantity}`;
		}
		yield new TaskProgress("Come back town", 12);
		return true;
	}
}



