import { Player, World, Activity, Group, Quest, LearnActivity } from '../../world';


export class Progress {

	private activities: { [id: string]: number } = {};

	public doneQuest(activity: Activity) {
		if (!activity.id) {
			console.dir(activity);
			throw 'missing activity id';
		}
		if (this.activities[activity.id]) {
			this.activities[activity.id]++;
		} else {
			this.activities[activity.id] = 1;
		}
	}

	public hasDoneQuest(activity: Activity) {
		if (!activity.id) {
			console.dir(activity);
			throw 'missing activity id';
		}
		return !!this.activities[activity.id];
	}
	public pushIfNotDone(activities : Activity[], activity: Activity) : boolean{
		if(this.hasDoneQuest(activity)){
			return false;
		}
		activities.push(activity);
		return true;
	}

	public pushIfUnknown(activities : Activity[], group: Group, activity: LearnActivity) : boolean{
		if(group.players.filter(p => activity.canLearn(p)).length == 0){
			return false;
		}		
		activities.push(activity);
		return true;
	}

	public pushFarmQuest(activities : Activity[], activity: Quest, world:World){
		const reward = activity.rewards[0];
		if(!reward){
			throw 'Not a farming quest';
		}
		if(world.bag.countItems(reward) < 10){
			activities.push(activity);
		}		
	}

}


