import { Component, OnInit } from '@angular/core';
import { Input, Output, EventEmitter } from '@angular/core';
import { Item, Food, Player, Tools } from '../world/';
import { Focus } from '../layout/focus';



interface AttributeDescComponent {
	label: string;
}

@Component({
	selector: 'app-item',
	templateUrl: './item.component.html',
	styleUrls: ['./item.component.css']
})
export class ItemComponent implements OnInit {

	@Input() item: Item;
	@Input() player: Player;
	@Input() quantity: number;
	@Input() clickable: boolean;
	@Output() click = new EventEmitter<Item>();

	constructor() { }

	ngOnInit(){}

  ngOnChanges() {
		if (this.quantity === 1) {
			this.quantity = null;
		}
	}

	onClick() {
		this.click.emit(this.item);
	}

	getAttributeDescriptions(): AttributeDescComponent[] {
		const attrs = this.item.getAttributeDescriptions();
		return attrs.map(a => <AttributeDescComponent>{ label: Tools.format(a.template, a.value) });
	}

}



