import {
	Tools, Logger,  Ability, Character,AbilityCondition
} from '../../world';


export class AbilitySelector {
	abilityConditions: AbilityCondition[] = [];
	eatIfLifePercentUnder = 90;
	
	choseAction(logger: Logger, self: Character, target: Character, myGroup: Character[],
		targets: Character[], tick: number): boolean {
		if(self.casting){
			if(self.casting.tick()){				
				const castingDone = self.casting.castingDone;
				self.casting.ability.casting = null;
				self.casting = null;

				castingDone(tick);
			}
			return;
		}
		for (let abilityCondition of this.abilityConditions) {
			if (abilityCondition.checkAction(logger, self, target, myGroup, targets, tick)) {
				return true;
			}
		}
		return false;
	}
}


