import {
	CharacterStat, Item, Logger, Player, AttributeDescription
} from '../../world';


export class Food extends Item {

	isFood = true;

	constructor(
		name: string,
		public restoreLife: number) {
		super(name);
		this.stackSize = 999;
	}

	consume(logger: Logger, player: Player) {
		const incr = Math.min(this.restoreLife, player.maxLife - player.currentLife);
		player.currentLife += incr;
		if (player.useMana) {
			const mana = Math.min(this.restoreLife, player.maxMana - player.currentMana);
			player.currentMana += mana;
		}

		logger.log(`${player.name} eat ${this.name} and regain ${incr} lifes`);
	}

	getAttributeDescriptions(): AttributeDescription[] {
		return [new AttributeDescription("food", this.restoreLife, "Consume to restore {0} lifes before a fight.")];
	}

}


