


export class Tools {


	static random = function (min: number, max: number) {
		return min + Math.random() * (max - min);
	}


	static randomPick(array: any[]) {
		return array[Math.floor(Math.random() * array.length)];
	}

	static removeItem<T>(list: T[], item: T): boolean {
		const index = list.indexOf(item);
		if (index != -1) {
			list.splice(index, 1);
			return true;
		}
		return false;
	}
	static distinctItems<T>(list: T[]): T[] {
		return list.filter((item, i, ar) => { return ar.indexOf(item) === i; });
	}
	static fillArray<T>(value: T, len: number): T[] {
		const arr: T[] = [];
		for (var i = 0; i < len; i++) {
			arr.push(value);
		}
		return arr;
	}

	public static format(value, ...args): string {
		try {
			return value.replace(/{(\d+(:.*)?)}/g, function (match, i) {
				var s = match.split(':');
				if (s.length > 1) {
					i = i[0];
					match = s[1].replace('}', '');
				}

				var arg = Tools.formatPattern(match, args[i]);
				return typeof arg != 'undefined' && arg != null ? arg : "";
			});
		}
		catch (e) {
			return "";
		}
	}
	private static formatPattern(match, arg): string {
		switch (match) {
			case 'L':
				arg = arg.toLowerCase();
				break;
			case 'U':
				arg = arg.toUpperCase();
				break;
			default:
				break;
		}

		return arg;
	}

	public static niceAssign(target, source) {
		return Object.assign(target, source);
	}
	public static arrayAssign<T>(target: T[], source: T[]) {
		target.length = source.length;
		for (let i = 0; i < source.length; i++) {
			target[i] = Tools.niceAssign(target[i] || {}, source[i]);
		}
	}


}




