import {
	ITask
} from '../../world';

export class Thread {

	private stack: IterableIterator<any>[] = [];		
	public allTasks: ITask[] = [];

	constructor(task: ITask | IterableIterator<any>) {
		this.push(task);
	}
	private push(task: ITask | IterableIterator<any>) {
		const asTask = <any>task;
		if (asTask.start) {
			this.stack.push(asTask.start());
			if(asTask.progress != null){
				this.allTasks.push(asTask);
			}			
		} else if ((<any>task).next) {
			this.stack.push(<IterableIterator<any>>task);
		} else {
			throw "invalid type: " + typeof task;
		}
	}

	private lastValue = null;
	public returnedValue = null;
	public tick(): boolean {
		if (this.stack.length == 0) {
			return false;
		}
		const top = this.stack[this.stack.length - 1];
		const next = top.next(this.lastValue);
		this.lastValue = next.value;		
		if (next.done) {			
			this.stack.pop();
		} else if (next.value) {
			if (next.value.next) {
				this.push(next.value);
			} else if (next.value.start) {
				this.push(next.value);
			}
		}
		this.returnedValue = this.lastValue;
		return true;
	}
 
	
}



