import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Input, Output, EventEmitter } from '@angular/core';
import { Ability, Tools, CastingHeal } from '../world/';
import { Observable } from 'rxjs/Observable';

@Component({
	selector: 'app-ability-icon',
	templateUrl: './ability-icon.component.html',
	styleUrls: ['./ability-icon.component.css'],
	changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AbilityIconComponent implements OnInit {

	@Input() ability: Ability;

	requirements$ : Observable<any[]>;
	manaCost$ : Observable<number>;
	castingDuration$ : Observable<number>;
	cooldown$ :  Observable<number>;
	
	constructor() { }

	ngOnInit() { 
		this.requirements$ = this.ability.$.map(a => a.requirements);
		this.cooldown$= this.ability.$.map(a =>  a.cooldown);
		this.manaCost$ = this.ability.$.map(a => a.manaCost);
		this.castingDuration$ = this.ability.$.map(a => a.castingDuration);				
	}

}



