import {
	Activity, ITask, Group, ILevel, TaskProgress
} from '../../world';



export class TravelActivity extends Activity {
	constructor(
		public destination: ILevel,
		direction: string
	) {
		super(`Travel ${direction} to ${destination.name}`);
		this.icon = destination.icon;//🦌
	}

	public start(group: Group): ITask {
		return new TaskProgress('Riding', 20);
	}

	onQuestSuccess(group: Group) {
		group.setLocation(this.destination);
	}
}


