import {
	Ability, Buff, InstantBuff, Swing, BuffType, ShieldBuff,
	DrinkPotion, AbilityCondition, CastingHeal, HighBlow
} from '../../world';



export class AbilityStore {


	static drinkPotion = () => AbilityStore.createDrinkPotion();



	static highBlow = () => new HighBlow('⛏', 'high blow', 50);
	static hotWoundAbility = () => AbilityStore.createDot('🌶', 'hot wound', 10, 9, 1);
	static stoneShield = () => AbilityStore.createStoneShield('🗿', 'stone shield');
	static cristalShield = () => AbilityStore.createCristalShield('🔮', 'cristal shield');

	static fastHeal = () => new CastingHeal('❣', 'fast heal', 15, 12, 10, 30, 1);
	static strongHeal = () => new CastingHeal('💖', 'strong heal', 35, 35, 10, 100, 3);
	//💞

	static poisonAbility = () => AbilityStore.createDot('☣', 'poisonous', 10, 15, 3);


	private static createDrinkPotion() {
		const d = new DrinkPotion('🍷', "drink potion", 600);
		d.defaultAbilityCondition = AbilityCondition.newMyLifeIsLess(d, 50);
		return d;
	}
	private static createDotBuff(buffType: BuffType, damage, during) {
		return new Buff(buffType,
			(source, target) => {
				return { damage: damage, during: during };
			},
			(tag) => `Do ${tag.damage} damages every 3 sec during ${tag.during} sec`,
			() => { },
			(source, target, age, tag) => {
				if (age > 1 && age % 30 == 0) {
					target.takeDamage(null, tag.damage);
				}
				return age < 10 * tag.during;
			});
	}
	private static createDot(icon: string, name: string, damage: number, during: number, buffStack: number) {
		const buffType = new BuffType(icon, name, true);
		const buff = AbilityStore.createDotBuff(buffType, damage, during);
		const ability = new InstantBuff(icon, name, buff, true, buffStack, 10);
		ability.description = `Do ${damage} damages every 3 sec during ${during} sec `;
		return ability;
	}

	private static createStoneShield(icon: string, name: string) {
		const buffType = new BuffType(icon, name, true);
		buffType.tag = "shield";
		const buff = new Buff(buffType,
			(source, target) => {
				return {};
			},
			(tag) => `Double the armor during 5 sec`,
			(self, characterStat) => {
				characterStat.armor.addPercent(() => 100);
			},
			(source, target, age, tag) => {
				return age < 50;
			});
		const ability = new ShieldBuff(icon, name, buff, 10);
		ability.description = `Double the armor during 5 sec. Only one type of shield ability can be active at any time.`;
		return ability;
	}

	private static createCristalShield(icon: string, name: string) {
		const buffType = new BuffType(icon, name, true);
		buffType.tag = "shield";
		const buff = new Buff(buffType,
			(source, target) => {
				const armor = source.characterStat.armor.getValue(source, null, target);
				return { reduction: armor };
			},
			(tag) => `Damage received is reduced by ${tag.reduction}`,
			(self, characterStat, tag) => {
				characterStat.damageReduction.add(() => tag.reduction);
			},
			(source, target, age, tag) => {
				return age < 50;
			});
		const ability = new ShieldBuff(icon, name, buff, 10);
		ability.description = `Reduce the damage by the level of armor during 5 sec. Only one type of shield ability can be active at any time.`;
		return ability;
	}

}




