import {
	Ability, Swing, CharacterStat, Character, EquipableItem, Logger, Group,
	Bag, Tools, Food, Potion, DrinkPotion, ILevel, Level1, LevelStore, AbilityStore, HighBlow
} from '../../world';




export class Player extends Character {

	public isPlayer = true;
	public group: Group;
	public equipedItems: EquipableItem[] = [];
	public location: ILevel;


	constructor(icon: string, name: string, public profession: string) {
		super(icon, name, 'player');
		if (this.profession === 'fighter') {
			this.characterStat.computeAttakPower = (strength: number, agility: number): number => {
				return strength * 2;
			}
			this.addAndUseAbility(AbilityStore.highBlow);
		}
		else if (this.profession === 'priest') {
			this.useMana = true;
			this.addAndUseAbility(AbilityStore.fastHeal);
			this.addAndUseAbility(AbilityStore.strongHeal);
		}
		else if (this.profession === 'necromancer') {
			this.useMana = true;
			this.addAndUseAbility(AbilityStore.poisonAbility);
			
		} else {
			throw 'unmanaged profession';
		}
		this.characterStat.stamina.initialValue = 7;
		this.refreshStat();
	}

	protected initCharaterStat() {
		for (let item of this.equipedItems) {
			item.addBuff(this.characterStat);
		}
		const weapons = this.equipedItems.filter(item => item.damage != 0);
		const mainWeapon = weapons[0];
		this.weaponDamage = (mainWeapon ? mainWeapon.damage : 3);
		this.weaponCooldown = (mainWeapon ? mainWeapon.damageCooldown : 15);
		this.weaponType = (mainWeapon ? mainWeapon.slot : 'hand');
	}

	hasShield() {
		return this.equipedItems.filter(item => item.slot === "off hand").length !== 0;
	}

	equip(item: EquipableItem, bag: Bag) {
		if (!item.isEquipable) {
			return;
		}
		for (let previousItem of this.equipedItems.filter(
			previousItem => item.useSameSlot(previousItem))) {
			if (Tools.removeItem(this.equipedItems, previousItem)) {
				if (bag) {
					bag.add(previousItem);
				}
			}
		}
		this.equipedItems.push(item);
		if (bag) {
			bag.remove(item);
		}
		this.refreshStat();
	}

	unequip(item: EquipableItem, bag: Bag) {
		if (Tools.removeItem(this.equipedItems, item)) {
			if (bag) {
				bag.add(item);
			}
			this.refreshStat();
		}
	}



	popFoodBeforeFight(logger: Logger, group: Group): Food {
		const lifePercent = this.currentLife * 100 / this.maxLife;
		if (lifePercent < 99 && lifePercent < this.abilitySelector.eatIfLifePercentUnder) {
			const food = group.world.bag.popFood();
			return food;
		}
		if (this.useMana && this.maxMana > 1) {
			const manaPercent = this.currentMana * 100 / this.maxMana;
			if (manaPercent < 99 && manaPercent < this.abilitySelector.eatIfLifePercentUnder) {
				const food = group.world.bag.popFood();
				return food;
			}
		}
		return null;
	}

	onAliveTick(tick: number) {
		super.onAliveTick(tick);
		if (this.useMana) {
			if (this.currentMana < this.maxMana) {
				const incr = this.regenManaPerSecond / 10;
				this.currentMana = Math.min(this.maxMana, this.currentMana + incr);
				this.dispatch();
			}
		}
	}
	rest(tick: number) {
		if (this.isDead) {
			this.isDead = false;
			this.currentLife = 1;
		}
		if (this.currentLife < this.maxLife) {
			const incr = Math.max(1, this.maxLife * 0.01);
			this.currentLife = Math.min(this.maxLife, this.currentLife + incr);
			this.dispatch();
		}
		if (this.useMana) {
			if (this.currentMana < this.maxMana) {
				const incr = Math.max(1, this.maxMana * 0.01);
				this.currentMana = Math.min(this.maxMana, this.currentMana + incr);
				this.dispatch();
			}
		}
	}

}


