import {
	Player
} from '../../world';


export class PlayerStore {


	public static Fighter1 = PlayerStore.createPlayer('🧖', 'Jane the warrior', 'fighter');
	public static Fighter2 = PlayerStore.createPlayer('🙋🏾‍♂️', 'Marg the fearless', 'fighter');
	public static Priest1 = PlayerStore.createPlayer('🧞‍♀️', 'Gana the wise', 'priest');
	public static Necromancer1 = PlayerStore.createPlayer('🧛🏿‍♀️', 'Hotep the gifted', 'necromancer');
	
	private static createPlayer(icon: string, name: string, profession: string): Player {
		const item = new Player(icon, name, profession);
		return item;
	}

}


