import {
	ILevel, LevelTools, World, Mob,
	Item, EquipableItem, FoodStore,
	Activity, Quest, KillQuest, Progress,
	TravelActivity, LevelStore, Ability, AbilityStore,
	PotionStore,
	LearnActivity,
	RecruitActivity,
	PlayerStore,
	Group
} from '../../world';

export class Level4 implements ILevel {

	name = 'the desert';
	icon = '🏜';
	private static levelId = 4;

	static scorpion = LevelTools.createFastMob('🦂', 'scorpion', 10, 600, 35);
	static scorpionTunic = LevelTools.createArmor('👕', 'scorpion tunic', { slot: 'chest', qualityColor: 'blue', wisdom: 5, strength: 5, armor: 5 });
	static scorpionLeggings = LevelTools.createArmor('👖', 'scorpion leggings', { slot: 'leg', qualityColor: 'blue', intellect: 5, stamina: 5, armor: 5 });
	static scorpionShoe = LevelTools.createArmor('👞', 'scorpion shoe', { slot: 'feet', qualityColor: 'blue', wisdom: 5, spirit: 5, stamina: 5 });
	static scorpionSword = LevelTools.createWeapon('🗡', 'scorpion sword', 15, 10, { slot: 'main hand', qualityColor: 'blue', intellect: 5, strength: 5, armor: 5 });
	static scorpionShield = LevelTools.createArmor('🛡', 'scorpion shield', { slot: 'off hand', qualityColor: 'blue', intellect: 5, spirit: 5, stamina: 5 });
	static fightScorpion = LevelTools.createKillQuest(Level4.levelId, Level4.scorpion, 1, [Level4.scorpionTunic, Level4.scorpionLeggings, Level4.scorpionShoe, Level4.scorpionSword, Level4.scorpionShield], "Desert master");


	getAvailableActivities(progress: Progress, world: World, group: Group): Activity[] {
		const activities = [];
		activities.push(new TravelActivity(LevelStore.level2, 'south'));
		progress.pushIfNotDone(activities, Level4.fightScorpion);


		return activities;
	}




}



