import {
	Activity, ITask, Group, ILevel, TaskProgress, Ability, Player
} from '../../world';



export class LearnActivity extends Activity {
	public ability: Ability;
	constructor(
		public profession: string,
		public abilityBuilder: () => Ability
	) {
		super(`Learn from the master`);
		this.icon = '👳🏼‍';
		this.ability = abilityBuilder();
		this.id = 'learn ' + this.ability.name;
	}

	public start(group: Group): ITask {
		return new TaskProgress('Learning ' + this.ability.name, 20);
	}

	canLearn(player:Player) : boolean{
		if(this.profession != null && this.profession != player.profession){
			return false;
		}
		if(player.abilities.filter(a => a.name == this.ability.name).length !==0){
			return false;
		}
		return true;
	}
	
	onQuestSuccess(group: Group) {
		for (let p of group.players.filter(p => this.canLearn(p))) {
			p.addAndUseAbility(this.abilityBuilder);
		}
	}
}


