import {
	Tools, Logger, Ability, Character, Buff
} from '../../world';



export class InstantBuff extends Ability {

	constructor(
		icon: string,
		name: string,
		public buff: Buff,
		public isDebuff: boolean,
		public buffCountLimit = 1,
		cooldown: number = 0) {
		super(icon, name);
		this.cooldown = cooldown;
	}
	protected cast(logger: Logger, self: Character, fightTarget: Character,
		myGroup: Character[], targets: Character[], conditionTarget: Character, tick: number): boolean {
		if (!conditionTarget) {
			return false;
		}
		const target = this.isDebuff ? fightTarget : (conditionTarget || self);
		if (target.isDead) {
			return false;
		}
		const buffList = this.isDebuff ? target.debuffs : target.buffs;
		if (buffList.appliedBuffs
			.filter(a => a.buff.buffType === this.buff.buffType).length
			 >= this.buffCountLimit) {
			return false;
		}
		buffList.addBuff(tick, self, target, this.buff);		
		this.setLastActionDesc(`Used on ${target.name}`, tick);
		
		return true;
	}
}


