import {
	ILevel, LevelTools, World, Mob, Tools,
	Item, EquipableItem, FoodStore, Level3,
	Activity, Quest, KillQuest, Progress,
	TravelActivity, LevelStore, Ability, AbilityStore,
	PotionStore,
	LearnActivity,
	Group,
	RecruitActivity,
	PlayerStore
} from '../../world';

export class Level2 implements ILevel {


	name = 'the town';
	icon = '🏯';

	static learnToDrinkPotion = new LearnActivity(null, AbilityStore.drinkPotion);
	static bat = LevelTools.createMob('🦇', 'bat', 40, 3);
	static farmPotion = LevelTools.createKillQuest(2, Level2.bat, 4, Tools.fillArray(PotionStore.smallPotion, 4), 'Get some potions');

	static rooster = LevelTools.createMob('🐓', 'rooster', 120, 11);
	static roosterLeggings = LevelTools.createArmor('👖', 'rooster leggings', { slot: 'leg', strength: 2, stamina: 2 });
	static killRooster = LevelTools.createKillQuest(2, Level2.rooster, 1, Level2.roosterLeggings, "The infected farm");

	static turtle = LevelTools.createMob('🐢', 'small turtle', 220, 8);
	static turtleLeggings = LevelTools.createArmor('👖', 'turtle leggings', { slot: 'leg', spirit: 2, stamina: 2 });
	static killTurtle = LevelTools.createKillQuest(2, Level2.turtle, 4, Level2.turtleLeggings, "Wild pit");

	static learnHotWound = new LearnActivity('fighter', AbilityStore.hotWoundAbility);
	static recruitFighter2 = new RecruitActivity(PlayerStore.Fighter2);
	static recruitNecromancer1 = new RecruitActivity(PlayerStore.Necromancer1);
	static learnPoisonAbility = new LearnActivity('necromancer', AbilityStore.poisonAbility);
	



	getAvailableActivities(progress: Progress, world: World, group: Group): Activity[] {
		const activities = [];
		activities.push(new TravelActivity(LevelStore.level1, 'south'));
		progress.pushIfUnknown(activities, group, Level2.learnToDrinkPotion);
		progress.pushIfNotDone(activities, Level2.killRooster);
		progress.pushIfNotDone(activities, Level2.killTurtle);
		if (progress.hasDoneQuest(Level2.killRooster) || progress.hasDoneQuest(Level2.killTurtle)) {
			progress.pushIfUnknown(activities, group, Level2.learnHotWound);
		}
		if (progress.hasDoneQuest(Level2.learnToDrinkPotion)) {
			progress.pushFarmQuest(activities, Level2.farmPotion, world);
		}
		if (progress.hasDoneQuest(Level2.learnHotWound)) {
			activities.push(new TravelActivity(LevelStore.level3, 'east'));
		}
		if (progress.hasDoneQuest(Level3.recruitPriest)) {
			progress.pushIfNotDone(activities, Level2.recruitFighter2);
			progress.pushIfNotDone(activities, Level2.recruitNecromancer1);
			progress.pushIfUnknown(activities, group, Level2.learnPoisonAbility);			
		}
		if (progress.hasDoneQuest(Level2.recruitFighter2)) {
			activities.push(new TravelActivity(LevelStore.level4, 'north'));
		}
		return activities;
	}




}



