import {
	ILevel, LevelTools, World, Mob,
	Item, EquipableItem, FoodStore,
	Activity, Quest, KillQuest, Progress,
	TravelActivity, LevelStore, Ability, AbilityStore,
	PotionStore,
	LearnActivity,
	RecruitActivity,
	PlayerStore,
	Group
} from '../../world';

export class Level3 implements ILevel {

	name = 'the temple';
	icon = '🕌';
	private static levelId = 3;

	static recruitPriest = new RecruitActivity(PlayerStore.Priest1);

	static hedgehog = LevelTools.createMob('🦔', 'hedgehog', 300, 10);
	static hedgehogTunic = LevelTools.createArmor('👗', 'hedgehog tunic', { slot: 'chest', spirit: 2, intellect: 2 });
	static hedgehogSword = LevelTools.createWeapon('🗡', 'hedgehog sword', 6, 25, { slot: 'main hand', stamina: 5 });
	static letsDress1 = LevelTools.createKillQuest(Level3.levelId, Level3.hedgehog, 1, [Level3.hedgehogTunic, Level3.hedgehogSword], "Let's dress");

	static blowFish = LevelTools.createMob('🐡', 'blowFish', 200, 16);
	static blowFishShoe = LevelTools.createArmor('👞', 'blowFish shoe', { slot: 'feet', wisdom: 2, stamina: 2 });
	static blowFishShield = LevelTools.createArmor('🛡', 'blowFish shield', { slot: 'off hand', armor: 3, stamina: 3 });
	static letsDress2 = LevelTools.createKillQuest(Level3.levelId, Level3.blowFish, 1, [Level3.blowFishShoe, Level3.blowFishShield], "Tie the shoelaces");

	static learnShield1 = new LearnActivity('fighter', AbilityStore.stoneShield);
	static learnShield2 = new LearnActivity('fighter', AbilityStore.cristalShield);


	getAvailableActivities(progress: Progress, world: World, group: Group): Activity[] {
		const activities = [];
		activities.push(new TravelActivity(LevelStore.level2, 'west'));
		progress.pushIfNotDone(activities, Level3.recruitPriest);
		progress.pushIfNotDone(activities, Level3.letsDress1);
		progress.pushIfNotDone(activities, Level3.letsDress2);

		if (progress.hasDoneQuest(Level3.letsDress1) || progress.hasDoneQuest(Level3.letsDress2)) {
			progress.pushIfUnknown(activities, group, Level3.learnShield1);
			progress.pushIfUnknown(activities, group, Level3.learnShield2);
		}

		return activities;
	}




}



