
import {
	AttributePipe, Character, Ability
} from '../../world';



export class CharacterStat {

	stamina: AttributePipe;
	strength: AttributePipe;
	agility: AttributePipe;
	intellect: AttributePipe;//Spell power
	spirit: AttributePipe;//Regen
	wisdom: AttributePipe;//Max mana
	armor: AttributePipe;
	damageReduction: AttributePipe;
	critPercent: AttributePipe;

	constructor() {
		this.stamina = new AttributePipe("stamina", 3, 1);
		this.strength = new AttributePipe("strength", 0);
		this.agility = new AttributePipe("agility", 0);
		this.intellect = new AttributePipe("intellect", 0);
		this.spirit = new AttributePipe("spirit", 10);
		this.wisdom = new AttributePipe("wisdom", 10);
		this.armor = new AttributePipe("armor", 0);
		this.damageReduction = new AttributePipe("damageReduction", 0);
		this.critPercent = new AttributePipe("critPercent", 0);
	}

	clear() {
		this.stamina.clear();
		this.strength.clear();
		this.agility.clear();
		this.intellect.clear();
		this.spirit.clear();
		this.wisdom.clear();
		this.armor.clear();
		this.damageReduction.clear();
		this.critPercent.clear();
	}

	getAttackPower(source: Character, ability: Ability, target: Character) {
		const strength = this.strength.getValue(source, ability, target);
		const agility = this.agility.getValue(source, ability, target);
		return this.computeAttakPower(strength, agility);
	}

	computeAttakPower = (strength: number, agility: number): number => {
		return strength;
	}
	armorReduceDamage(damage: number, self: Character, ability: Ability, target: Character): number {
		if (damage < 1) {
			return 0;
		}
		const armor = this.armor.getValue(self, ability, target);
		const armorReducedDamage = Math.floor(damage * armor / (10 + armor));
		const damageReduction = this.damageReduction.getValue(self, ability, target);
		const endDamage = Math.max(1, Math.round(damage - armorReducedDamage - damageReduction));
		return endDamage;
	}

}


