import {
	Quest, Mob, ITask, Group, KillQuestTask
} from '../../world';



export class KillQuest extends Quest {	
	mobSample : Mob;
	constructor(
		public mobBuilder: () => Mob,
		public quantity: number,
	) {		
		super("");
		this.mobSample =this.mobBuilder();		
		this.description = this.name = `Kill ${quantity} ${this.mobSample.name}s`;
		this.icon = this.mobSample.icon;
	}

	
	public start(group: Group): ITask {
		return new KillQuestTask(group, this);
	}
}


