import {
	Tools, Logger, Ability, Character, Buff
} from '../../world';



export class ShieldBuff extends Ability {

	constructor(
		icon: string,
		name: string,
		public buff: Buff,
		public cooldown: number) {
		super(icon, name);
		if (buff.buffType.tag !== "shield") {
			throw 'invalid buff tag';
		}
		this.requirements = [{ description: 'Requires a shield', ok: true }];
	}

	onCharacterChanges(character: Character) {
		super.onCharacterChanges(character);
		this.requirements = [{ description: 'Requires a shield', ok: character.hasShield()}];		
		this.dispatch();
	}

	protected cast(logger: Logger, self: Character, fightTarget: Character,
		myGroup: Character[], targets: Character[], conditionTarget: Character, tick: number): boolean {
		if (!conditionTarget) {
			return false;
		}		
		if(!self.hasShield()){
			return false;
		}
		if (self.buffs.appliedBuffs.filter(a => a.buff.buffType.tag === "shield").length !== 0) {
			return false;
		}
		self.buffs.addBuff(tick, self, self, this.buff);
		this.setLastActionDesc(`Refreshed`, tick);
		return true;
	}
}


