import {
	Item, Potion
} from '../../world';


export class PotionStore {


 static smallPotion = PotionStore.createPotion('❤', 'small potion', 50);
 
 private static createPotion(icon: string, name:string, life:number): Potion{
	 const item = new Potion(icon, name, life);
	 item.icon = icon;
	 return item;
 }



}




