
import {
	CharacterStat, Character
} from '../../world';


export class BuffType {
	constructor(
		public icon: string,
		public name: string,
		public isVisible: boolean) {
	}
	public tag: string;
}

export class Buff {

	public description: string;
	constructor(
		public buffType: BuffType,
		private _onCast: (caster: Character, target: Character) => any,
		private _descriptionFunc: (tag: any) => string,
		public applyStat: (caster: Character, characterStat: CharacterStat, tag:any) => void,
		public tick: (caster: Character, target: Character, buffAgeTick: number, tag: any) => boolean
	) {
	}

	public onCast(caster: Character, target: Character): any {
		const tag = this._onCast(caster, target);
		this.description = this._descriptionFunc(tag);
		return tag;
	}
}



