import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {
	Tools, Logger, World, Character, AbilityCondition, Casting
} from '../../world';



export abstract class Ability {
	$ = new BehaviorSubject(this);

	cooldown: number = 0;
	lastCastTick: number = -6000;
	lastActionDesc: string = '';
	lastActionTick: number = -6000;
	description: string;
	defaultAbilityCondition: AbilityCondition;
	casting: Casting;	
	castingDuration: number;
	manaCost : number;
	requirements : {description:string, ok:boolean}[] = [];	
	constructor(
		public icon: string,
		public name: string) {
	}
	public dispatch(){
		this.$.next(this);
	}

	onCharacterChanges(character: Character) {
	}

	setLastActionDesc(description: string, tick: number) {
		this.lastActionDesc = description;
		this.lastActionTick = tick;		
	}

	tickCast(logger: Logger, tick: number,
		self: Character, target: Character,
		myGroup: Character[], targets: Character[], conditionTarget: Character): boolean {
		if ((tick - this.lastCastTick) < this.cooldown) {
			return false;
		}
		const ok = this.cast(logger, self, target, myGroup, targets, conditionTarget, tick);
		if (ok) {
			this.lastCastTick = tick;
			self.dispatchAbilities();
		}
		return ok;
	}
	protected abstract cast(logger: Logger, self: Character, target: Character,
		myGroup: Character[], targets: Character[], conditionTarget: Character, tick: number): boolean;


	protected getBaseDamage(dmgMultiplier: number, self: Character, target: Character) {
		const ap = self.characterStat.getAttackPower(self, this, target);
		const apBonus = ap * self.weaponCooldown / 14;
		return (self.weaponDamage + apBonus) * dmgMultiplier;
	}

	protected doPhysicalDamage(dmgMultiplier: number, logger: Logger, self: Character, target: Character, tick: number) {
		const baseDmg = this.getBaseDamage(dmgMultiplier,self, target);
		const dmg = Math.floor(baseDmg * Tools.random(0.5, 1.5));
		const reducedDamage = target.characterStat.armorReduceDamage(dmg, self, this, target);
		logger.log(`${self.name} do ${reducedDamage} damages to ${target.name} with a ${this.name}`);
		target.takeDamage(logger, reducedDamage);
		this.setLastActionDesc(`Done ${reducedDamage} damages`, tick);
	}

}


