import {
	Tools, Logger, Ability, Character
} from '../../world';



export class Swing extends Ability {

	constructor(icon: string, name: string) {
		super(icon, name);
		this.cooldown = 1;
		this.description = `Basic attack using current weapon`;
	}

	onCharacterChanges(character: Character) {
		super.onCharacterChanges(character);
		this.cooldown = character.weaponCooldown;
		const baseDmg = Math.round(this.getBaseDamage(1, character, null));
		this.description = `Basic attack doing ${baseDmg} damages using current weapon`;
		this.dispatch();
	}
 
	protected cast(logger: Logger, self: Character, target: Character,
		myGroup: Character[], targets: Character[], conditionTarget: Character, tick: number): boolean {
		if (!conditionTarget) {
			return false;
		}
		this.cooldown = self.weaponCooldown;
		this.doPhysicalDamage(1, logger, self, target, tick);
		return true;
	}
}


