import { Component, OnInit } from '@angular/core';
import { Input, Output } from '@angular/core';
import { World, Group, Activity, ILevel } from '../world/';
import { Focus } from '../layout/focus';

@Component({
	selector: 'app-quest-dashboard',
	templateUrl: './quest-dashboard.component.html',
	styleUrls: ['./quest-dashboard.component.css']
})
export class QuestDashboardComponent implements OnInit {

	@Input() focus: Focus;
	@Input() quests: Activity[];
	@Input() location: ILevel;
	selectedQuest : Activity;
	


	constructor() {	
	}
	
	ngOnInit(){}

  ngOnChanges() {
		this.selectedQuest = null;
	}

	click(quest: Activity){
		this.selectedQuest = quest;
	}

	startQuest(){
		this.focus.world.startActivity(this.focus.selectedGroup, this.selectedQuest);
		this.focus.refresh();
	}

}



