import { Component, OnInit } from '@angular/core';
import { Input, Output } from '@angular/core';
import { Player, AbilityCondition } from '../world/';
import { Focus } from '../layout/focus';

@Component({
  selector: 'app-ability-usage',
  templateUrl: './ability-usage.component.html',
  styleUrls: ['./ability-usage.component.css']
})
export class AbilityUsageComponent implements OnInit {

	@Input() focus: Focus;
	@Input() player: Player;
  constructor() { }

	ngOnInit(){}

  ngOnChanges() {	
	}
	
	remove(abilityCondition){
		const index = this.player.abilitySelector.abilityConditions.indexOf(abilityCondition);
		if(index >= 0){
			this.player.abilitySelector.abilityConditions.splice(index, 1);
			this.player.dispatchAbilities();
		}
	}
	add(){
		const cond = new AbilityCondition();
		cond.ability = this.player.abilities[0];
		this.player.abilitySelector.abilityConditions.push(cond);
		this.player.dispatchAbilities();
	}

}



