import {
	CharacterStat, Item, Logger, Player, AttributeDescription
} from '../../world';


export class Potion extends Item {

	isPotion = true;

	constructor(
		icon: string,
		name: string,
		public restoreLife: number) {
		super(name);
		this.icon = icon;
		this.stackSize = 999;
	}

	getAttributeDescriptions(): AttributeDescription[] {
		return [new AttributeDescription("potion", this.restoreLife, "Consume to restore {0} lifes during a fight.")];
	}

}


