
import {
	Character, Ability
} from '../../world';



export class AttributePipe {
	private addPipe = [];
	private addPercentPipe = [];
	constructor(
		public name: string,
		public initialValue: number,
		public minValue = 0
	) {
	}

	clear() {
		this.addPipe.length = 0;
		this.addPercentPipe.length = 0;
	}

	add(func: (source: Character, ability: Ability, target: Character) => number) {
		this.addPipe.push(func);
	}
	addPercent(func: (source: Character, ability: Ability, target: Character) => number) {
		this.addPercentPipe.push(func);
	}

	getValue(source: Character, ability: Ability, target: Character) {
		let baseValue = this.initialValue;
		for (let func of this.addPipe) {
			baseValue += func(source, ability, target);
		}
		let multiply = 100;
		for (let func of this.addPercentPipe) {
			multiply += func(source, ability, target);
		}
		return Math.max(this.minValue, Math.round(baseValue * multiply / 100));
	}

	get defaultValue() {
		return this.getValue(null, null, null);
	}
}


