

import {
	ITask, Group
} from '../../world';



export abstract class Activity {
	id: string;
	icon: string;
	description: string;
	constructor(public name: string) {
		this.id = this.name;
		this.description = this.name;
	}
	public abstract start(group: Group): ITask;
	public abstract onQuestSuccess(group: Group);
}



