import {
	ILevel, LevelTools, World, Mob,Tools,
	Item, EquipableItem, FoodStore,
	Activity, Quest, KillQuest, Progress, TravelActivity, Store, LevelStore, Group
} from '../../world';

export class Level1 implements ILevel {


	name = 'the home';
	icon = '💒';

	//name = 'the camp';
	//icon = '🏕';

	static mouse = LevelTools.createMob('🐁', 'mouse', 10, 2);
	static spoonSword = LevelTools.createWeapon('🥄', 'spoon sword', 6, 21, {qualityColor: 'white', slot: 'two-hand' });
	static firstQuest = LevelTools.createKillQuest(1, Level1.mouse, 2, Level1.spoonSword);

	static findSomeFood = LevelTools.createKillQuest(1, Level1.mouse, 2, Tools.fillArray(FoodStore.carrot, 3), 'Find some food');

	static rat = LevelTools.createMob('🐀', 'rat', 30, 3);
	static needleSword = LevelTools.createWeapon('⚔', 'needle sword', 7, 21, { slot: 'two-hand', strength: 1, stamina: 1 });	
	static findNeedleSword = LevelTools.createKillQuest(1, Level1.rat, 5, Level1.needleSword);

	static hamster = LevelTools.createMob('🐹', 'hamster', 70, 5);
	static hamsterHelmet = LevelTools.createArmor('👕', 'hamster tunic', { slot: 'chest', armor: 2, stamina: 2 });
	static findHelmet = LevelTools.createKillQuest(1, Level1.hamster, 1, Level1.hamsterHelmet, "The haunter hamster");

	static boar = LevelTools.createMob('🐗', 'boar', 120, 8);
	static beatBoar = LevelTools.createKillQuest(1, Level1.boar, 1, [], "The dangerous forest");






	getAvailableActivities(progress: Progress, world: World, group: Group): Activity[] {
		const activities = [];
		if (progress.pushIfNotDone(activities, Level1.firstQuest)) {
			return activities;
		}
		if (world.bag.countItems(Level1.findSomeFood.rewards[0]) < 12) {
			activities.push(Level1.findSomeFood);
		}
		progress.pushIfNotDone(activities, Level1.findNeedleSword);
		progress.pushIfNotDone(activities, Level1.findHelmet);
		progress.pushIfNotDone(activities, Level1.beatBoar);
		if (progress.hasDoneQuest(Level1.beatBoar)) {
			activities.push(new TravelActivity(LevelStore.level2, 'north'));
		}

		return activities;
	}


}


