import { Component, OnInit } from '@angular/core';
import { Input, Output, EventEmitter } from '@angular/core';
import { Mob, Tools } from '../world/';
import { Focus } from '../layout/focus';

@Component({
  selector: 'app-mob',
  templateUrl: './mob.component.html',
  styleUrls: ['./mob.component.css']
})
export class MobComponent implements OnInit {

  constructor() { }

	@Input() mob: Mob;
	@Input() focus: Focus;
	dps = 0;
	difficulty = 'normal';

	ngOnInit(){}

  ngOnChanges() {
		this.dps = this.mob.getWeaponDps();
		if(this.focus && this.focus.selectedGroup){
			this.difficulty = this.focus.selectedGroup.getFightDifficulty(this.mob);
		}		
  }

}



