import {
	Item, Food
} from '../../world';


export class FoodStore {


 static carrot = FoodStore.createFood('🥕', 'carrot', 30);

 private static createFood(icon: string, name:string, life:number): Food{
	 const item = new Food(name, life);
	 item.icon = icon;
	 return item;
 }



}




