import {
	Tools, Logger, Ability, Character
} from '../../world';


export class AbilityCondition {

	public ability: Ability;
	public static allTypes = ['always', 'lifeIsLess', 'lifeIsMore'];
	type: string = 'always';
	values: any[] = [];


	static newMyLifeIsLess(ability: Ability, percent: number): AbilityCondition {
		const c = new AbilityCondition();
		c.ability = ability;
		c.type = 'lifeIsLess';
		c.values = ['myself', percent];
		return c;
	}

	checkAction(logger: Logger, self: Character, target: Character, myGroup: Character[],
		targets: Character[], tick: number): boolean {
		const conditionTarget = this.evaluate(self, target, myGroup, targets);
		return this.ability.tickCast(logger, tick, self, target, myGroup, targets, conditionTarget);
	}





	evaluate(self: Character, target: Character,
		myGroup: Character[], targets: Character[]): Character {
		switch (this.type) {
			case 'always': return target;
			case 'lifeIsLess': return this.lifeIsLess(self, target, myGroup, targets, true);
			case 'lifeIsMore': return this.lifeIsLess(self, target, myGroup, targets, false);
		}

		return null;
	}

	private lifeIsLess(self: Character, target: Character,
		myGroup: Character[], targets: Character[], isLess: boolean): Character {
		const targetType = this.values[0];
		const lifePercent = this.values[1];
		if (!lifePercent) {
			return null;
		}
		let lookIn: Character[];
		switch (targetType) {
			case 'myself': lookIn = [self]; break;
			case 'tank': lookIn = [myGroup[0]]; break;
			case 'mob': lookIn = [target]; break;
			case 'friend': lookIn = myGroup; break;
			default : throw 'unknow option: ' + targetType;
		}
		for (let c of lookIn) {
			if (isLess) {
				if (c.getCurrentLifePercent() < lifePercent) {
					return c;
				}
			} else {
				if (c.getCurrentLifePercent() > lifePercent) {
					return c;
				}
			}
		}
		return null;
	}

}



