
import {
	ITask
} from '../../world';



export class TaskProgress implements ITask {
	private progressTick = 0;
	public progress = '';
	constructor(
		public name: string,
		public endTick: number,
	) {
	}

	public *start() {
		for (; this.progressTick <= this.endTick; this.progressTick++) {
			this.progress = Math.round(this.progressTick * 100 / this.endTick) + '%';
			yield;
		}
		return true;
	}
}


