import {
	Activity, Item, Group, Ability
} from '../../world';



export abstract class Quest extends Activity {
	public rewards: Item[] = [];
		
	constructor(name: string) {
		super(name)
	}

	onQuestSuccess(group : Group){
		if (this.rewards) {
			for (const reward of this.rewards) {
				group.loot(reward);
			}
		}
	}

}



