import {
	CharacterStat, Item, AttributeDescription
} from '../../world';



export class EquipableItem extends Item {
	isEquipable = true;
	slot: string = null;
	damage: number = 0;
	damageCooldown: number = 0;
	armor: number = 0;
	stamina: number = 0;
	strength: number = 0;
	agility: number = 0;
	intellect: number = 0;
	spirit: number = 0;
	wisdom: number = 0;
	
	constructor(name: string) {
		super(name);
		this.stackSize = 1;
	}


	public useSameSlot(item: EquipableItem) :boolean{
		if(this.slot === item.slot){
			return true;
		}
		if(this.slot === "main hand" || this.slot == "off hand"){
			if(item.slot === "two-hand"){
				return true;
			}
		}
		if(item.slot === "main hand" || item.slot == "off hand"){
			if(this.slot === "two-hand"){
				return true;
			}
		}
		return false;
	}

	public addBuff(characterStat: CharacterStat): void {
		if (this.armor != 0) {
			characterStat.armor.add(() => this.armor);
		}
		if (this.stamina != 0) {
			characterStat.stamina.add(() => this.stamina);
		}
		if (this.strength != 0) {
			characterStat.strength.add(() =>  this.strength);
		}
		if (this.agility != 0) {
			characterStat.agility.add(() => this.agility);
		}
		if (this.intellect != 0) {
			characterStat.intellect.add(() =>this.intellect);
		}
		if (this.spirit != 0) {
			characterStat.spirit.add( () =>  this.spirit);
		}
		if (this.wisdom != 0) {
			characterStat.wisdom.add(() =>  this.wisdom);
		}
		
	}


	getAttributeDescriptions(): AttributeDescription[] {
		const attr = [];
		if (this.damage != 0) {
			const dps = Math.round(this.damage * 100 / this.damageCooldown) / 10;
			const speed = this.damageCooldown / 10;
			attr.push(new AttributeDescription("damage", dps, `${this.damage} damages every ${speed} sec (${dps} dps)`));
		}
		if (this.armor != 0) {
			attr.push(new AttributeDescription("armor", this.armor, "+ {0} armor"));
		}
		if (this.stamina != 0) {
			attr.push(new AttributeDescription("stamina", this.stamina, "+ {0} stamina"));
		}
		if (this.strength != 0) {
			attr.push(new AttributeDescription("strength", this.strength, "+ {0} strength"));
		}
		if (this.agility != 0) {
			attr.push(new AttributeDescription("agility", this.agility, "+ {0} agility"));
		}
		if (this.intellect != 0) {
			attr.push(new AttributeDescription("intellect", this.intellect, "+ {0} intellect"));
		}
		if (this.spirit != 0) {
			attr.push(new AttributeDescription("spirit", this.spirit, "+ {0} spirit"));
		}		
		if (this.wisdom != 0) {
			attr.push(new AttributeDescription("wisdom", this.wisdom, "+ {0} wisdom"));
		}
		return attr;

	}
}



