import {
	Item, EquipableItem
} from '../../world';


export class EquipableStore {

//🏹 ⛏ ⚔ 👞 👚 🧥 🧤  👖  🕶  ⛑ 💍 💎
 static bronzeSword = EquipableStore.createWeapon('🗡', 'bronze sword');
 static bronzeChest = EquipableStore.createArmor('🛡', 'bronze chest');

 private static createWeapon(icon: string, name:string): Item{
	const item = new EquipableItem(name);
	item.slot = "two-hand";
	item.stamina = 2;
	item.strength = 2;
	item.damageCooldown = 21;
	item.damage = 10;
	item.icon = icon;
	item.qualityColor = "green";
	return item;
}

 private static createArmor(icon: string, name:string): Item{
	 const item = new EquipableItem(name);
	 item.icon = icon;
	 item.slot = "chest";
	 item.armor = 2;
	 item.stamina = 3;
	 item.strength = 2;
	 item.qualityColor = "green";
	 return item;
 }



}



