
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {
	Logger, Group, AbilityCondition, AbilitySelector, Ability,
	Swing, CharacterStat, EquipableItem, Bag, Tools, BuffList, Casting

} from '../../world';




export abstract class Character {

	$ = new BehaviorSubject(this);
	abilities$ = new BehaviorSubject(this);

	characterStat: CharacterStat = new CharacterStat();
	isDead: boolean = false;
	currentLife: number = 300;
	maxLife: number = 300;
	weaponDamage = 0;
	weaponCooldown = 1;
	weaponType = 'nude';//nude, main-hand, two-hand, both-hand
	abilities: Ability[] = [];
	globalCooldown = 5;
	lastActionTick = -100;
	buffs = new BuffList();
	debuffs = new BuffList();
	casting: Casting;
	useMana = false;
	currentMana = 0;
	maxMana = 1;
	regenManaPerSecond = 0;

	getCurrentLifePercent(): number {
		return Math.round(this.currentLife * 100 / this.maxLife);
	}

	abilitySelector: AbilitySelector = new AbilitySelector();

	constructor(public icon: string, public name: string, public type: string) {
		this.addAndUseAbility(() => new Swing('🔨', "basic attack"));
	}

	public dispatch(){
		this.$.next(this);
	}
	public dispatchAbilities(){
		this.abilities$.next(this);
	}

	refreshStat() {
		this.characterStat.clear();
		this.initCharaterStat();
		this.buffs.initCharaterStat(this.characterStat);
		this.debuffs.initCharaterStat(this.characterStat);

		this.maxLife = Math.max(10, this.characterStat.stamina.getValue(this, null, null) * 10);
		this.currentLife = Math.min(this.currentLife, this.maxLife);
		if (this.useMana) {
			this.maxMana = Math.max(10, this.characterStat.wisdom.getValue(this, null, null) * 10);
			this.currentMana = Math.min(this.currentMana, this.maxMana);
			this.regenManaPerSecond = Math.max(1, this.characterStat.spirit.getValue(this, null, null) / 10);
		}
		for(let ability of this.abilities){
			ability.onCharacterChanges(this);
		}
		this.dispatch();
	}

	protected abstract initCharaterStat();

	addAndUseAbility(abilityBuilder: () => Ability) {
		const ability = abilityBuilder();
		this.abilities.push(ability);

		const abilityCondition = ability.defaultAbilityCondition || new AbilityCondition();
		abilityCondition.ability = ability;
		this.abilitySelector.abilityConditions.push(abilityCondition);
	}



	fightAction(logger: Logger, target: Character, myGroup: Character[], targets: Character[], tick: number) {
		if (tick - this.lastActionTick < this.globalCooldown) {
			return;
		}
		if (this.abilitySelector.choseAction(logger, this, target, myGroup, targets, tick)) {
			this.lastActionTick = tick;
		}
	}

	getCooldownPercent(ability: Ability, tick: number): number {
		if (this.isDead) {
			return 0;
		}
		const duration = tick - ability.lastCastTick;
		if (ability.cooldown <= 0 || duration >= ability.cooldown) {
			return 100;
		}
		return Math.round(duration * 100 / ability.cooldown);
	}

	getActiveAbilities(): Ability[] {
		return Tools.distinctItems(this.abilitySelector.abilityConditions.map(s => s.ability));
	}

	getWeaponDps() {
		const ap = this.characterStat.getAttackPower(null, null, null);
		const weaponDps = this.weaponDamage * 10 / this.weaponCooldown;
		return Math.round(weaponDps + ap);
	}

	abstract hasShield();

	evaluateDps() {
		return this.getWeaponDps() * this.getActiveAbilities().length;
	}

	takeDamage(logger: Logger, dmg: number) {
		this.currentLife -= dmg;
		if (!this.isDead && this.currentLife <= 0) {
			this.currentLife = 0;
			this.isDead = true;
			this.buffs.clear();
			this.debuffs.clear();			
			console.log(`${this.icon} ${this.name} is dead`);			
			this.dispatch();
		}
	}

	getArmoredLife(): number {
		return this.maxLife * (10 + this.characterStat.armor.getValue(null, null, null)) / 10;
	}

	onTick(tick: number) {
		if (this.isDead) {
			return;
		}
		this.onAliveTick(tick);
	}

	onAliveTick(tick: number) {
		this.buffs.tick(this, tick);
		this.debuffs.tick(this, tick);
	}

}


