import { Injectable } from '@angular/core';
import { Player, World } from '../../world';




@Injectable()
export class Store {
	public static readonly WORLD_SAVE = "WORLD_SAVE";
	private _storage = window.localStorage;
	public world: World;

	constructor() {
		try {
			this.world = JSON.parse(Store.WORLD_SAVE);
		} catch{ }
	}

	save() {
		this._storage.setItem(Store.WORLD_SAVE, JSON.stringify(this.world));
	}

	clear() {
		this._storage.removeItem(Store.WORLD_SAVE);
		this.world = null;
	}
}


