import { Component, OnInit } from '@angular/core';
import { Input, Output } from '@angular/core';
import { Tools, World, Group, Player } from '../world/';
import { Focus } from '../layout/focus';

@Component({
	selector: 'app-edit-group',
	templateUrl: './edit-group.component.html',
	styleUrls: ['./edit-group.component.css']
})
export class EditGroupComponent implements OnInit {
	@Input() focus: Focus;
	@Input() group: Group;
	availablePlayers: Player[];

	constructor() { }

	ngOnInit() { }

	ngOnChanges() {
		this.refresh();
	}

	refresh() {
		this.availablePlayers = this.group.world.players.filter(
			p => this.group.players.indexOf(p) === -1);
	}

	inludePlayer(player: Player) {
		this.group.includePlayer(player);
		this.refresh();
	}

	excludePlayer(player: Player) {
		this.group.excludePlayer(player);
		this.refresh();
		if (player === this.focus.selectedPlayer) {
			this.focus.selectGroup(player, player.group);
		}
	}

	promotePlayer(player: Player) {
		this.group.promotePlayer(player);
	}

}



