
import {
	CharacterStat, Character, Buff, World, Tools
} from '../../world';


export class AppliedBuff {
	constructor(
		public buff: Buff,
		public caster: Character,
		public startTick: number,
		public tag: any) {
	}
}

export class BuffList {
	public appliedBuffs: AppliedBuff[] = [];



	public addBuff(tick: number, caster: Character, target: Character, buff: Buff) {
		const tag = buff.onCast(caster, target);
		const appliedBuff = new AppliedBuff(buff, caster, tick, tag);
		this.appliedBuffs.push(appliedBuff);
		target.refreshStat();
	}


	public initCharaterStat(characterStat: CharacterStat) {
		for (let appliedBuff of this.appliedBuffs) {
			appliedBuff.buff.applyStat(appliedBuff.caster, characterStat, appliedBuff.tag);
		}
	}

	public tick(character: Character, tick: number) {
		let changed = false;
		for (let appliedBuff of this.appliedBuffs) {
			const ok = appliedBuff.buff.tick(appliedBuff.caster, character, tick - appliedBuff.startTick, appliedBuff.tag);
			if (!ok) {
				Tools.removeItem(this.appliedBuffs, appliedBuff);
				changed = true;
			}
		}
		if (changed) {
			character.refreshStat();
		}
	}

	public clear() {
		this.appliedBuffs.length = 0;
	}


}



