import {
	Tools, Logger, Ability, Character, Buff
} from '../../world';



export class Casting {

	public percent = 0;
	private tickCount = 0;

	constructor(
		private durationTick: number,
		public ability:Ability,
		public castingDone: (tick) => void
	) {	
	}
	public tick(): boolean {
		this.tickCount++;
		if (this.durationTick != 0) {
			this.percent = Math.min(100, Math.round(this.tickCount * 100 / this.durationTick));
		}
		return this.tickCount >= this.durationTick;
	}
}


