
import {
	CharacterStat, Character		
} from '../../world';





export class Mob extends Character {
	public isMob = true;

	constructor(icon: string, name: string, life: number, dps: number) {
		super(icon, name, 'mob');
		
		this.weaponCooldown = 26;
		this.weaponDamage = Math.round(this.weaponCooldown * dps / 20);
		this.characterStat.strength.initialValue = Math.max(1, Math.round(dps / 2));	
		this.characterStat.stamina.initialValue = Math.max(1, Math.round(life/10));		
		this.refreshStat();
	}

	

	protected initCharaterStat() {				
	}

	public repop(){
		this.isDead = false;
		for(let ability of this.abilities){
			ability.lastCastTick =  -1;
		}
		this.refreshStat();
		this.currentLife = this.maxLife;
	}

	public hasShield(){
		return true;
	}

}


